import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { environment } from 'src/environments/environment';
import { map } from 'rxjs/internal/operators/map';
import { Observable } from 'rxjs/internal/Observable';
import { MatSnackBar } from '@angular/material';
import { FormGroup } from '@angular/forms';

@Injectable({
  providedIn: 'root'
})
export class CommonService {

  constructor(public http: HttpClient, public snackBar?: MatSnackBar) {

  }

  getAllCountrys() {
    return this.http.get(environment.baseUrl + environment.location.country).pipe(
      map((res: any) => res.data)
    );
  }
  getAllStates(countryId: number) {
    return this.http.get(environment.baseUrl + environment.location.state + countryId).pipe(
      map((res: any) => res.data)
    );
  }
  getAllCities() {
    return this.http.get(environment.baseUrl + environment.location.cities).pipe(
      map((res: any) => res.data)
    );
  }
  getIdentityTypes() {
    return this.http.get(environment.baseUrl + environment.utils.identitys).pipe(
      map((res: any) => res.data)
    );
  }
  getUomList(): Observable<any> {
    return this.http.get(environment.baseUrl + environment.utils.uom).pipe(
      map((res: any) => res.data)
    );
  }

  getRegions() {
    return this.http.get(environment.baseUrl + environment.product.region).pipe(
      map((res: any) => res.data)
    );
  }
  showSnackBar(message: string, action: string) {
    this.snackBar.open(message, action, {
      duration: 2000,
      panelClass: ['red-snackbar']
    });
  }

  validate(form: FormGroup) {
    Object.keys(form.controls).forEach(field => {
      const control = form.get(field);
      control.markAsDirty({ onlySelf: true });
    });
  }
}
