import { NgModule } from '@angular/core';
import { RouterModule } from '@angular/router';
import { ToolbarModule } from './components/toolbar/toolbar.module';
import { FooterModule } from './components/footer/footer.module';
import { CommonModule } from '@angular/common';
import { MatDrawer, MatSidenavModule } from '@angular/material';
import { MaterialModule } from '../material.module';
import { HaribolLayoutComponent } from './haribol-layout.component';
import { FlexLayoutModule } from '@angular/flex-layout';
import { SidebarModule } from './components/sidebar/sidebar.module';
import { AddNewComponent } from '../shared/components/add-new/add-new.component';
import { SharedModule } from '../shared/shared.module';


@NgModule({
  declarations: [
    HaribolLayoutComponent
  ],
  imports: [
    MatSidenavModule,
    CommonModule,
    FlexLayoutModule,
    RouterModule.forChild([
      {
        path: '',
        redirectTo: 'haribol',
        pathMatch: 'full'
      },
      {
        path: 'haribol',
        component: HaribolLayoutComponent,
        children: [
          {
            path: 'customer',
            loadChildren: '../features/customer/customer.module#CustomerModule'
          },
          {
            path: 'product',
            loadChildren: '../features/product/product.module#ProductModule'
          },
          {
            path: 'delivery-channel',
            loadChildren: '../features/delivery-channel/delivery-channel.module#DeliveryChannelModule'
          },
          {
            path: 'schedule-delivery',
            loadChildren: '../features/schedule-delivery/schedule-delivery.module#ScheduleDeliveryModule'
          },
          {
            path: 'user',
            loadChildren: '../features/user/user.module#UserModule'
          },
          {
            path: 'addnew',
            component: AddNewComponent
          },
        ]
      }, {
        path: '',
        redirectTo: 'customer',
        pathMatch: 'full'
      }


    ]),
    SidebarModule,
    FooterModule,
    ToolbarModule,
    SharedModule,
    MaterialModule
  ],
  exports: [
    HaribolLayoutComponent,
    AddNewComponent
  ]
})
export class HaribolLayoutModule {
}
