import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';
import { Router } from '@angular/router';

@Component({
  selector: 'haribol-page-heading',
  templateUrl: './page-heading.component.html',
  styleUrls: ['./page-heading.component.scss']
})
export class PageHeadingComponent implements OnInit {
  @Input() title: string;
  @Input() headIcon: string;
  @Input() btnText: string;
  @Input() count: number;
  @Input() searchRequire: any = true;
  @Output() searchVal: EventEmitter<any> = new EventEmitter();
  @Output() btnClick: EventEmitter<any> = new EventEmitter();
  constructor(public router: Router) { }

  ngOnInit() {
    this.count = 0;
    console.log('this.searchRequire', this.searchRequire);
  }
  public searchClick(event) {
    this.searchVal.emit(event.target.value);
  }
  public addnewBtnClick() {
    this.btnClick.emit(true);
  }
}
