import { Injectable } from '@angular/core';
import { Observable } from 'rxjs/internal/Observable';
import { environment } from 'src/environments/environment';
import { HttpClient } from '@angular/common/http';
import { map } from 'rxjs/operators';

@Injectable({
  providedIn: 'root'
})
export class CommonUtilsService {

  constructor(public http: HttpClient) { }
  getPaymentTypes(): Observable<any> {
    return this.http.get(environment.baseUrl + environment.utils.payTypes).pipe(
      map((res: any) => res.data)
    );
  }

  createTransactions(data: any) {
    return this.http.post(environment.baseUrl + environment.transactions.url, data).pipe(
      map((res: any) => res.data)
    );
  }

  getAllTransactions(customerId: number) {
    return this.http.get(environment.baseUrl + environment.transactions.list + customerId).pipe(
      map((res: any) => res.data)
    );
  }
}
