import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { FormGroup, FormBuilder, Validators } from '@angular/forms';
import { AuthenticateService } from 'src/app/shared/services/authenticate.service';
import { CommonService } from 'src/app/shared/services/common-service.service';

@Component({
  selector: 'haribol-login',
  templateUrl: './login.component.html',
  styleUrls: ['./login.component.scss']
})
export class LoginComponent implements OnInit {
  loginForm: FormGroup;
  public errorMsg: string;
  constructor(public router: Router,
    public authenticateService: AuthenticateService,
    private _fb: FormBuilder,
    public commonService: CommonService) { }

  ngOnInit() {
    /**
     * loginForm formgroup intiated for login credentials.
     */
    this.loginForm = this._fb.group({
      username: ['', Validators.required],
      password: ['', Validators.required]
    });
  }
  /**
   * loginClick method for checking user login.
   */
  public loginClick() {
    this.errorMsg = null;
    if (this.loginForm.valid) {
      this.authenticateService.login(this.loginForm.value).subscribe(res => {
        localStorage.setItem('token', res.accessToken);
        this.commonService.showSnackBar('Success!!', 'Loggedin Successfully');
        this.router.navigate(['haribol']);
      }, err => {
        this.errorMsg = err.error.error + ', ' + err.error.message;

      });
    } else {
      this.commonService.validate(this.loginForm);
    }
  }

}
