@Component({
  selector: 'haribol-data-table',
  templateUrl: './data-table.component.html',
  styleUrls: ['./data-table.component.scss']
})
export class DataTableComponent implements OnInit, OnChanges {

  @Input() filterVal: string;
  @Input() dataSource: any[];
  @Output() editClick: EventEmitter<any> = new EventEmitter();
  @Output() removeClick: EventEmitter<any> = new EventEmitter();
  rows = [];
  columns = [
    { name: 'Name', prop: 'name' },
    { name: 'Description', prop: 'description' },
    { name: 'Quantity', prop: 'quantity' },
    { name: 'hasDispensibleContainer', prop: 'hasDispensibleContainer' },
    { name: 'HSN', prop: 'hsn' },
    { name: 'Price', prop: 'basePrice' },
    { name: 'Actions' },
  ];
  constructor() {
  }
  editBtnClick(product) {
    this.editClick.emit(product);
  }
  removeBtnClick(product) {
    this.removeClick.emit(product);

  }
  ngOnChanges(changes: SimpleChanges) {
    if (changes.filterVal.currentValue || changes.filterVal.currentValue === '') {
      const filterStr = changes.filterVal.currentValue.toLowerCase();

      // filter our data
      let tempArr = this.rows.filter(function (d) {
        return d.name.toLowerCase().indexOf(filterStr) !== -1 || d.description.toLowerCase().indexOf(filterStr) !== -1;
      });
      if (filterStr === '') {
        tempArr = this.dataSource;
      }
      this.rows = tempArr;
    }


  }

  ngOnInit() {
    this.rows = this.dataSource;
    // this.dataSourceArr = new MatTableDataSource(this.dataSource);
  }

}
