import { Component, OnInit } from '@angular/core';
import { FormGroup, FormBuilder } from '@angular/forms';
import { PaymentModes } from 'src/app/core/models/payment-modes';
import { CommonUtilsService } from './../../services/common-utils.service';

@Component({
  selector: 'haribol-add-credit-dialog',
  templateUrl: './add-credit-dialog.component.html',
  styleUrls: ['./add-credit-dialog.component.scss']
})
export class AddCreditDialogComponent implements OnInit {
  public creditForm: FormGroup;
  public paymentModes: any;

  constructor(public fb: FormBuilder, public commonUtilService: CommonUtilsService) { }

  ngOnInit() {
    this.paymentModes = PaymentModes.types();
    this.creditForm = this.fb.group({
      date: [],
      type: [],
      details: [],
      amount: []
    });
    this.commonUtilService.getPaymentTypes().subscribe(res => {
      this.paymentModes = res;
    });
  }
  addCredit() {
    console.log(this.creditForm);
  }

}
