import { Component, OnDestroy, OnInit, ViewEncapsulation } from '@angular/core';
import { Subject } from 'rxjs';
import { Router } from '@angular/router';
import { SidenavService } from './../shared/services/sidenav.service';
import { environment } from '../../environments/environment';
import { RoutingService } from '../shared/services/routing.service';

@Component({
  selector: 'haribol-layout',
  templateUrl: './haribol-layout.component.html',
  styleUrls: ['./haribol-layout.component.scss'],
  encapsulation: ViewEncapsulation.None
})
export class HaribolLayoutComponent implements OnInit {
  public isSideNav = false;
  constructor(public router: Router, public sidenavService: SidenavService, public routingService: RoutingService) { }

  ngOnInit(): void {
    this.sidenavService.currentSideNavClass.subscribe(sidenavCls => this.isSideNav = sidenavCls);
    this.routingService.goToPage('customer');
  }
  public changeWidth() {
    // this.isSideNav = !this.isSideNav;
  }

}
