import { Component, ViewEncapsulation, OnInit, NgZone } from '@angular/core';
import { Router } from '@angular/router';
import { SidenavService } from 'src/app/shared/services/sidenav.service';
import { RoutingService } from 'src/app/shared/services/routing.service';
import { MediaObserver, MediaChange } from '@angular/flex-layout';
import { Subscription } from 'rxjs/internal/Subscription';
@Component({
  selector: 'haribol-sidebar',
  templateUrl: './sidebar.component.html',
  styleUrls: ['./sidebar.component.scss'],
  encapsulation: ViewEncapsulation.None
})
export class SidebarComponent implements OnInit {
  public panelOpenState = true;
  public sideNavClass = 'sidenav';
  public isProductExpanded = true;
  public isCustomerExpanded = true;
  watcher: Subscription;
  activeMediaQuery = '';
  constructor(public router: Router, public sidenavService: SidenavService,
    public routingService: RoutingService,
    public mediaObserver: MediaObserver,
    public zone: NgZone
  ) { }
  public ngOnInit() {
    this.sidenavService.currentSideNavClass.subscribe(productExpand => {
      this.isProductExpanded = true;
      this.isCustomerExpanded = true;
    });
    this.mediaObserver.media$.subscribe((change: MediaChange) => {
      if (change.mqAlias === 'xs' || change.mqAlias === 'sm') {
        console.log(change.mqAlias);
        this.sidenavService.changeClass(true);
      } else {
        this.sidenavService.changeClass(false);
      }
    });

  }
  public redirectTo(page) {
    this.routingService.goToPage(page);
  }
  public arrowClick() {
    this.panelOpenState = !this.panelOpenState;
    if (this.panelOpenState) {
      this.sidenavService.changeClass(false);
    } else {
      this.sidenavService.changeClass(true);

    }
  }
}
